<template>
  <div class="auth">
    <div class="form">
      <h1>Регистрация</h1>
      <input @input="error = ''" type="text" v-model="userData.login" placeholder="Логин">
      <input @input="error = ''" type="text" v-model="userData.phone" ref="field" placeholder="Номер телефона">
      <input @input="error = ''" type="password" v-model="userData.password" placeholder="Пароль">
      <input @input="error = ''" type="password" v-model="userData.doublePassword" placeholder="Повторите пароль">
      <div class="form-group">
        <router-link to="/auth" class="form-recovery" href="#">Авторизация</router-link>
      </div>
      <button @click="register">
        Зарегистрироваться
        <div id="loader" v-if="loaderView"></div>
      </button>
      <div v-if="msg" class="regmsg">
        <p>{{msg}}</p>
      </div>
    </div>
  </div>
</template>

<script>

import inputmask from "inputmask";

export default {
  data() {
    return {
      userData: {
        login: '',
        phone: '',
        password: '',
        doublePassword: ''
      },
      msg: '',
      loaderView: false
    }
  },
  mounted () {
    // ... используем
    var im = new inputmask("+7 (999) 999-99-99");
    im.mask(this.$refs.field);
  },
  methods: {
    register() {
      this.loaderView = true;
      if(this.userData.login && this.userData.phone && this.userData.password) {
        if(this.userData.phone.slice(-1) != '_') {
          if(this.userData.password == this.userData.doublePassword) {
            let formdata = new FormData();
            formdata.append("login", this.userData.login);
            formdata.append("phone", this.userData.phone);
            formdata.append("password", this.userData.password);

            let requestOptions = {
              method: 'POST',
              body: formdata,
              redirect: 'follow'
            };

            fetch("https://diplom-api.na4u.ru/api/auth/signup", requestOptions)
              .then(response => response.json())
              .then(result => {
                if (result.values.message == 'Успешная регистрация') {
                  console.log(result)
                  this.$router.push({ path: "/auth" });
                } else {
                  this.msg = result.values.message
                  this.loaderView = false
                }
              })
              .catch(error => console.log('error', error));
          } else {
            this.msg = 'Пароли не совпадают'
            this.loaderView = false
          }
        } else {
          this.msg = 'Номер телефона должен состоять из 11 символов'
          this.loaderView = false
        }
      } else {
        this.msg = 'Не все поля заполнены'
        this.loaderView = false
      }
    }
  }
}
</script>
